import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import YouTube from 'react-youtube';
import KeyboardEventHandler from 'react-keyboard-event-handler';
import { NOT_STARTED, CUED, PAUSED, PLAYING } from '../../const/YoutubePlayerState';
import { FormGroup, FormControl, InputGroup, Panel, Button, PanelGroup, ControlLabel, Form } from 'react-bootstrap';
import moment from 'moment';
import { filterToNumberStr } from '../../util/util-string';
import { isUpdatedMoment } from '../../util/util-moment'
import ProgressPublisher from './progressPublisher'
var _ = require('underscore');

class YoutubePlayerBorder extends Component {

    youtubePlayer = null;
    // progressTimeId = null;
    sendingEvent = {
        youtubeIdUpdateEvent: {
            moment: null,
            youtubeId: null,
        },
        addSubtitleBeginTimeEvent: {
            moment: null,
            time: null,
        },
    };

    currentEvent  = {
        subtitleBorderEvent: {
            playPhaseEvent: {
                moment: null,
                startTime: null,
                endTime: null
            }
        }
    };

    progressPublisher = new ProgressPublisher();

    constructor(props) {
        super(props);

        this.state = {
            youtubeId: 'kze1JkPl5_8',
            preSeekInterval: "1",
            nextSeekInterval: "1",
            progressTime: 0,
        };

        this.progressPublisher.setOnProgress(this.onProgress);
    }

    componentWillMount() {
        this.sendingEvent.youtubeIdUpdateEvent.youtubeId = this.state.youtubeId;
        this.sendingEvent.youtubeIdUpdateEvent.moment = new moment();
        this.props.dispatch(this.createAction());
    }

    componentWillReceiveProps(newProps) {
        this.update(newProps);
    }

    update = (newProps) => {
        let curPlayPhaseEvent = this.currentEvent.subtitleBorderEvent.playPhaseEvent;
        let newPlayPhaseEvent = newProps.subtitleBorderEvent.playPhaseEvent;
        if (isUpdatedMoment(curPlayPhaseEvent.moment, newPlayPhaseEvent.moment)) {
            this.currentEvent.subtitleBorderEvent.playPhaseEvent = _.clone(newPlayPhaseEvent);
            this.playPhase(newPlayPhaseEvent.startTime, newPlayPhaseEvent.endTime);
        }
    };

    playPhase = (startTime, endTime) => {
        this.youtubePlayer.seekTo(startTime);
        this.youtubePlayer.playVideo();
        this.progressPublisher.setAlarm(endTime, ()=>{
            this.youtubePlayer.pauseVideo();
        });
    }

    onYoutubeReady = (e) => {
        this.youtubePlayer = e.target;
        this.progressPublisher.onReady(e);
    };

    onYoutubeStateChanged = (e) => {
        this.progressPublisher.onStateChanged(e);
    }

    createAction = () => {
        return { type: 'YoutubePlayerBorder', payload: Object.assign({}, _.clone(this.sendingEvent)) }
    };

    onLoadButtonClick = (e) => {
        this.sendingEvent.youtubeIdUpdateEvent.moment = new moment();
        this.sendingEvent.youtubeIdUpdateEvent.youtubeId = this.state.youtubeId;
        this.props.dispatch(this.createAction());
        this.youtubePlayer.cueVideoById(this.state.youtubeId);
        this.clearProgressTime();
    };

    onKeyPressed = (key) => {
        if (this.youtubePlayer === null)
            return;

        let playerState = this.youtubePlayer.getPlayerState();

        switch (key) {
            case 'space':
                // console.log(playerState);
                if (playerState === CUED) {
                    this.youtubePlayer.playVideo();
                } else if (playerState === NOT_STARTED || playerState === PAUSED) {
                    this.youtubePlayer.playVideo();
                } else if (playerState === PLAYING) {
                    this.youtubePlayer.pauseVideo();
                }
                break;
            case 'left':
                if (playerState === PLAYING || playerState === PAUSED) {
                    let seek = this.youtubePlayer.getCurrentTime() - parseFloat(this.state.preSeekInterval);
                    this.youtubePlayer.pauseVideo();
                    this.youtubePlayer.seekTo(seek);
                }
                break;
            case 'right':
                if (playerState === PLAYING || playerState === PAUSED) {
                    let seek = this.youtubePlayer.getCurrentTime() + parseFloat(this.state.nextSeekInterval);
                    this.youtubePlayer.pauseVideo();
                    this.youtubePlayer.seekTo(seek);
                }
                break;
            case 'a':
            case 'A':
                if (playerState === PLAYING || playerState === PAUSED) {
                    this.sendingEvent.addSubtitleBeginTimeEvent.moment = new moment();
                    this.sendingEvent.addSubtitleBeginTimeEvent.time = this.youtubePlayer.getCurrentTime();
                    this.props.dispatch(this.createAction());
                }
                break;
        }
    };

    onProgress = (curTime) => {
        this.setState({progressTime: curTime});
    };

    clearProgressTime = () => {
        this.setState({progressTime: 0});
    };

    render() {
        const opts = {
            width: '100%',
            playerVars: { // https://developers.google.com/youtube/player_parameters
                autoplay: 0,
            },
        };

        const offsetInputStyle = {
            width: '50px',
            marginLeft: '15px',
            marginRight: '5px',
            textAlign: 'center',
        };

        return (
            <div style={ { position: 'relative', width: '100%', height: 'auto', boxSizing: 'border-box' } }>
                <PanelGroup>
                    <Panel bsStyle="primary">
                        <Panel.Body>
                            <KeyboardEventHandler
                                handleKeys={ ['space', 'left', 'right', 'a', 'A'] }
                                onKeyEvent={ (key, e) => this.onKeyPressed(key) }/>
                            <div style={ { position: 'relative' } }>
                                <FormGroup>
                                    <InputGroup>
                                        <FormControl type="text" placeholder='youtube id'
                                                     onChange={ (e) => {
                                                         this.setState({ youtubeId: e.target.value });
                                                     } } value={ this.state.youtubeId }/>
                                        <InputGroup.Button>
                                            <Button bsStyle="primary" onClick={ this.onLoadButtonClick }>LOAD</Button>
                                        </InputGroup.Button>
                                    </InputGroup>
                                </FormGroup>
                            </div>
                            <YouTube
                                videoId={this.state.youtubeId}
                                opts={ opts }
                                onReady={ this.onYoutubeReady }
                                onStateChange={ this.onYoutubeStateChanged }>
                            </YouTube>
                        </Panel.Body>
                    </Panel>
                    <Panel>
                        <Panel.Heading>
                            <Panel.Body style={{position:'relative', height:'auto', padding:'0px'}}>
                                <div style={ {position:'relative', height:'100%', marginBottom:'0px',borderWidth:'0px', borderColor:'red', borderStyle:'solid'} }>
                                    <Form inline>
                                        <FormGroup>
                                            <ControlLabel style={ { fontWeight: 'normal' } }>Pre seek interval (left)</ControlLabel>
                                            <InputGroup style={ offsetInputStyle }>
                                                <FormControl type="text" value={ this.state.preSeekInterval }
                                                             onChange={
                                                                 (e) => {
                                                                     let str = filterToNumberStr(e.target.value);
                                                                     let value = parseFloat(str);
                                                                     if (isNaN(value)) {
                                                                         value = 0;
                                                                     }
                                                                     this.setState({preSeekInterval:str});
                                                                 }
                                                             }/>
                                            </InputGroup>
                                            <ControlLabel style={ { fontWeight: 'normal', marginLeft: '15px' } }>Next seek interval (right)</ControlLabel>
                                            <InputGroup style={ offsetInputStyle }>
                                                <FormControl type="text" value={ this.state.nextSeekInterval }
                                                             onChange={
                                                                 (e) => {
                                                                     let str = filterToNumberStr(e.target.value);
                                                                     let value = parseFloat(str);
                                                                     if (isNaN(value)) {
                                                                         str = "0";
                                                                     }
                                                                     this.setState({nextSeekInterval:str});
                                                                 }
                                                             }/>
                                            </InputGroup>
                                        </FormGroup>
                                    </Form>
                                    <div style={{height:'1px', borderWidth:'1px', borderStyle:'solid', borderColor:'#C0C0C0', marginTop:'15px', marginBottom:'15px'}}/>
                                    <Form inline>
                                        <FormGroup>
                                            <ControlLabel style={ { fontWeight: 'normal' } }>Progress time</ControlLabel>
                                            <InputGroup style={{marginLeft:'15px'}}>
                                                <FormControl type="text" value={ this.state.progressTime }/>
                                            </InputGroup>
                                        </FormGroup>
                                    </Form>
                                    <ControlLabel style={ { marginTop: '15px', fontWeight: 'normal' } }>(Press A key to insert subtitle at this progress time)</ControlLabel>
                                </div>
                            </Panel.Body>
                        </Panel.Heading>
                    </Panel>
                </PanelGroup>
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        subtitleBorderEvent: state.subtitleBorderEvent.payload,
    };
};

export default connect(mapStateToProps)(YoutubePlayerBorder);
