import { combineReducers } from 'redux';
import moment from 'moment';

const createYoutubePayload = () => {
    return {
        payload: {
            youtubeIdUpdateEvent: {
                moment: moment(),
                youtubeId: null,
            },
            addSubtitleBeginTimeEvent: {
                moment: null,
                time: null,
            },
        },
    };
};

const youtubePlayerBorderEvent = (state = createYoutubePayload(), action) => {
    switch (action.type) {
        case 'YoutubePlayerBorder':
            return { payload: action.payload };
    }
    return state;
};

const createSubtitleBorderPayload = () => {
    return {
        payload: {
            playPhaseEvent: {
                moment: null,
                startTime: null,
                endTime: null
            }
        },
    }
};

const subtitleBorderEvent = (state = createSubtitleBorderPayload(), action) => {
    switch (action.type) {
        case 'SubtitleBorder':
            return { payload: action.payload };
    }
    return state;
};

export default combineReducers({
    youtubePlayerBorderEvent,
    subtitleBorderEvent,
});
