import moment from 'moment';

export const formatToHHmmss_ss = (sec) => {
    let longValue = sec * 1000;
    let duration = moment.duration(longValue);
    let str = '';
    str += duration.hours() >= 10 ? duration.hours() + ':' : '0' + duration.hours() + ':';
    str += duration.minutes() >= 10 ? duration.minutes() + ':' : '0' + duration.minutes() + ':';
    str += duration.seconds() >= 10 ? duration.seconds() + '.' : '0' + duration.seconds() + '.';
    let millisecondsStr = Math.floor(duration.milliseconds()).toString();
    const len = millisecondsStr.length;
    for (var i = 0; i < 3-len; i++) {
        millisecondsStr = "0" + millisecondsStr;
    }
    str += millisecondsStr;
    // console.log('formatToHHmmss_ss');
    return str;
};

export const parseHHmmss_ss = (str) => {
    let split = str.split(".");
    if (split.length === 2 && split[1].length > 3) {
        str = split[0] + "." + split[1].substring(0, 3);
    }
    let longValue = moment.duration(str).valueOf();
    return longValue / 1000;
};

export const isUpdatedMoment = (currentMoment, newMoment) => {
    console.log(currentMoment);
    console.log(newMoment);
    if (newMoment === null)
        return false;
    if (currentMoment === null || currentMoment < newMoment)
        return true;
    return false;
};
