import React from 'react';
import {
    Checkbox,
    ControlLabel,
    Form,
    Glyphicon,
    FormGroup,
    FormControl,
    InputGroup,
    Panel,
    Button,
} from 'react-bootstrap';
import fetch from 'cross-fetch';
import { formatToHHmmss_ss, parseHHmmss_ss } from '../../util/util-moment'

class SubtitleBorderElement extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            english: props.english,
            kor: props.kor,
            start: props.start,
            startStr : props.startStr,//formatToHHmmss_ss(props.start),
            end: props.end,
            endStr : props.endStr, //formatToHHmmss_ss(props.end),
            learn: props.learn,
        };

        this.onBeginInputBlur = this.props.onBeginInputBlur;
        this.onEndInputBlur = this.props.onEndInputBlur;
    }

    static defaultProps = {
        domHeight: 204
    }

    componentWillReceiveProps(newProps) {
        this.updateStateFromProp(newProps);
    }

    updateStateFromProp = (newProps) => {
        this.setState(Object.assign({}, {
            english: newProps.english,
            kor: newProps.kor,
            start: newProps.start,
            startStr : newProps.startStr,
            end: newProps.end,
            endStr : newProps.endStr, //formatToHHmmss_ss(props.end),
            learn: newProps.learn,
        }));
    };

    onEnglishTextChanged = (e) => {
        this.setState(Object.assign({}, { english: e.target.value }));
    };

    onKorTextChanged = (e) => {
        this.setState(Object.assign({}, { kor:e.target.value }));
    };

    componentDidUpdate(prevProps, prevState) {
        console.log(' ele - componentDidUpdate');
        if (prevProps === this.props)
            this.props.onStateChanged(this.props, this.state);
    }

    onRemoveButtonClick = (e) => {
        this.props.onRemoveButtonClicked(this.props.index);
    };

    onAddButtonClick = (e) => {
        this.props.onAddButtonClicked(this.props.index);
    }

    onPlayButtonClick = (e) => {
        this.props.onPlayButtonClicked({
            startTime: this.state.start,
            endTime: this.state.end
        });
    };

    onCheckChanged = (e) => {
        this.setState({ learn: e.target.checked });
        // console.log(this.state.learn);
    };

    render() {
        const timeInputStyle = {
            width: '100px',
            marginLeft: '5px',
            marginRight: '5px',
            textAlign: 'center',
        };

        const textAreaStyle = {
            width: '100%',
            height: '80px',
            resize: 'none',
        };

        return (
                <Panel.Body style={
                        {
                            position: 'relative',
                            background: '#e9ecef',
                            padding: '0px',
                            height: 'auto',//this.props.domHeight + "px",
                            borderWidth: this.props.focus ? '5px' : '0px',
                            borderColor: 'red',
                            borderStyle: 'dotted',
                            boxSizing: 'border-box',
                        }
                    }>
                    <div style={{position:"relative", width:"100%", borderStyle:"solid", borderColor:"#C0C0C0", borderWidth:"1px"}} />
                    <div style={{position:"relative", marginTop:"15px"}}/>
                    <div style={{float: 'left', position:"relative", marginLeft:"15px", borderStyle:"solid", borderColor:"red", borderWidth:"0px", height:'auto'}}>
                        <div style={ { float: 'left', width:'36px', borderStyle:"solid", borderColor:"red", borderWidth:"0px" } }>
                            <Button bsSize="small" onClick={ this.onRemoveButtonClick }>
                                <Glyphicon glyph="minus"/>
                            </Button>
                            <Button style={{marginTop:'5px'}} bsSize="small" onClick={ this.onAddButtonClick }>
                                <Glyphicon glyph="plus"/>
                            </Button>
                        </div>
                        <div style={ { position:"relative", float: 'left', height: "100%", borderStyle:"solid", borderColor:"red", borderWidth:"0px" } }>
                            <Form inline>
                                <FormGroup>
                                    <InputGroup style={ timeInputStyle }>
                                        <FormControl type="text"
                                                     value={
                                                         this.state.startStr
                                                     }
                                                     onChange={
                                                         (e) => {
                                                             this.setState(Object.assign({}, { startStr: e.target.value }));
                                                         }
                                                     }
                                                     onBlur= {
                                                         (e) => {
                                                             let sec = parseHHmmss_ss(e.target.value);
                                                             let str = formatToHHmmss_ss(sec);
                                                             this.setState(Object.assign({}, { start: sec, startStr : str }));
                                                             this.onBeginInputBlur && this.onBeginInputBlur(this.props.index);
                                                         }
                                                     }
                                                     onKeyPress={(e) => {
                                                         if (e.charCode === 13) {
                                                             let str = this.state.startStr;
                                                             let sec = parseHHmmss_ss(str);
                                                             this.setState(Object.assign({}, { start: sec, startStr : str }));
                                                             this.onBeginInputPressed();
                                                         }
                                                     }}
                                        />
                                    </InputGroup>
                                    <ControlLabel>~</ControlLabel>
                                    <InputGroup style={ timeInputStyle }>
                                        <FormControl type="text" value={ this.state.endStr }
                                             onChange={
                                                (e) => {
                                                    this.setState(Object.assign({}, { endStr: e.target.value }));
                                                }
                                             }
                                             onBlur={
                                                 (e) => {
                                                     let sec = parseHHmmss_ss(e.target.value);
                                                     if (sec < this.state.start) {
                                                         sec = this.state.start;
                                                     }
                                                     let str = formatToHHmmss_ss(sec);
                                                     this.setState(Object.assign({}, { end: sec, endStr : str }));
                                                     this.onEndInputBlur && this.onEndInputBlur(this.props.index);
                                                 }
                                             }
                                             onKeyPress={(e) => {
                                                 if (e.charCode === 13) {
                                                     let str = this.state.endStr;
                                                     let sec = parseHHmmss_ss(str);
                                                     if (sec < this.state.start) {
                                                         sec = this.state.start;
                                                     }
                                                     str = formatToHHmmss_ss(sec);
                                                     this.setState(Object.assign({}, { end: sec, endStr : str }));
                                                     this.onEndInputBlur && this.onEndInputBlur(this.props.index);
                                                     // this.onBeginInputPressed();
                                                 }
                                             }}
                                        />
                                    </InputGroup>
                                </FormGroup>
                            </Form>
                            <Checkbox style={ { marginLeft: '5px' } } checked={ this.state.learn }
                                      onChange={ this.onCheckChanged }>Learning</Checkbox>
                        </div>
                        <div style={ { float: 'left', width: '266px', marginRight:'15px', marginBottom:'15px', boxSizing: 'border-box', borderWidth:'0px', borderColor:'red', borderStyle:'solid'} }>
                            <FormGroup>
                                <FormControl style={ textAreaStyle } componentClass="textarea" placeholder=""
                                             value={ this.state.english } onChange={ this.onEnglishTextChanged }/>
                                <div style={ { marginTop: '5px' } }></div>
                                <FormControl style={ textAreaStyle } componentClass="textarea" placeholder=""
                                             value={this.state.kor} onChange={this.onKorTextChanged}/>
                            </FormGroup>
                            <div style={ {position:'relative', float: 'right', clear: 'left', borderWidth:'0px', borderColor:'red', borderStyle:'solid'} }>
                                {/*<Button bsSize="small" onClick={ this.onTranslateButtonClick }>TRANSLATE</Button>*/}
                                <Button bsSize="small" style={{marginLeft:'5px'}} onClick={ this.onPlayButtonClick }>PLAY</Button>
                            </div>
                        </div>
                    </div>
            </Panel.Body>
        );
    }
}

export default SubtitleBorderElement;
