import React, { Component } from 'react';
import { connect } from 'react-redux';
import fetch from 'cross-fetch';
import SubtitleBorderElement from './SubtitleBorderElement';
import { PanelGroup, Button, ControlLabel, FormGroup, InputGroup, FormControl, Form, Panel } from 'react-bootstrap';
import 'react-confirm-alert/src/react-confirm-alert.css';
import moment from 'moment';
import {isUpdatedMoment} from '../../util/util-moment'
import {simpleAlert} from '../../util/util-dialog'
import Loader from 'react-loader-spinner'
import { formatToHHmmss_ss, parseHHmmss_ss } from '../../util/util-moment'

var _ = require('underscore');
var xml2js = require('xml2js');
var convert = require('xml-js');

class SubtitleBorder extends Component {

    currentEvent = {
        youtubePlayerBorderEvent: {
            youtubeIdUpdateEvent: {
                moment: null,
                youtubeId: null,
            },
            addSubtitleBeginTimeEvent: {
                moment: null,
                time: null,
            },
        },
    };

    sendingEvent = {
        playPhaseEvent: {
            moment: null,
            startTime: null,
            endTime: null
        }
    }

    dom = null;
    element = null;
    fileReader = null;

    constructor(props) {
        super(props);
        this.state = {
            transcript: [],
            nearNext: '',
            loading: false,
        };
        this.update = this.update.bind(this);
        this.fetchTranscriptXml = this.fetchTranscriptXml.bind(this);
        this.dom = document.createElement('div');
        this.element = document.createElement("a");
        this.fileReader = new FileReader();
        this.fileReader.onload = () => {
            this.onFileContentLoaded(this.fileReader.result);
        };
    }

    onFileContentLoaded = (content) => {
        try {
            let rawJson = JSON.parse(convert.xml2json(content, { compact: true, spaces: 4 }));
            console.log(rawJson);
            let transcript = [];
            if (rawJson.transcript && rawJson.transcript.text && rawJson.transcript.text.length > 0) {
                rawJson.transcript.text.map(e => {
                    let attributes = e._attributes;
                    e.dur = parseFloat(attributes.dur);
                    e.start = parseFloat(attributes.start);
                    e.end = e.start + e.dur;
                    e.startStr = formatToHHmmss_ss(e.start);
                    e.endStr = formatToHHmmss_ss(e.end);
                    e.learn = attributes.learning === 'true' ? true : false;
                    e.english = e._text;
                    e.kor = attributes.kor === undefined ? "" : attributes.kor;
                    delete e._attributes;
                    delete e._text;
                    return e;
                });
                transcript = rawJson.transcript.text;
            }
            console.log(transcript);
            this.setState({ transcript: transcript });
            this.refs.fileUploader.value = "";
        } catch (e) {
            simpleAlert('파일에 문제가 있습니다.');
        }
    };

    componentWillReceiveProps(newProps) {
        this.update(newProps);
    }

    showLoading(value) {
        this.setState({loading : value})
    }

    update = async (newProps) => {
        console.log(newProps);

        let newYoutubeIdUpdateEvent = newProps.youtubePlayerBorderEvent.youtubeIdUpdateEvent;
        let currentYoutubeIdUpdateEvent = this.currentEvent.youtubePlayerBorderEvent.youtubeIdUpdateEvent;

        if (isUpdatedMoment(currentYoutubeIdUpdateEvent.moment, newYoutubeIdUpdateEvent.moment)) {
            this.showLoading(true);
            this.currentEvent.youtubePlayerBorderEvent.youtubeIdUpdateEvent = _.clone(newYoutubeIdUpdateEvent);
            console.log(newYoutubeIdUpdateEvent.youtubeId);
            if (newYoutubeIdUpdateEvent.youtubeId !== null) {
                try {
                    let transcript = await this.fetchTranscriptXml(newYoutubeIdUpdateEvent.youtubeId);
                    if (transcript.length == 0) {
                        transcript[0] = {
                            start: 0,
                            dur: 0,
                            end: 1,
                            startStr: formatToHHmmss_ss(0),
                            endStr: formatToHHmmss_ss(1),
                            english: "",
                            learn: false,
                            focus: false,
                            kor: ""
                        };
                    }
                    this.setState({ transcript: transcript });
                    console.log(transcript);
                    this.showLoading(false);
                } catch (e) {
                    simpleAlert('youtubeId가 유효하지 않습니다.');
                    this.showLoading(false);
                }
            }
            return;
        }

        // ---

        let newAddSubtitleBeginTimeEvent = newProps.youtubePlayerBorderEvent.addSubtitleBeginTimeEvent;
        let currentAddSubtitleBeginTimeEvent = this.currentEvent.youtubePlayerBorderEvent.addSubtitleBeginTimeEvent;
        if (isUpdatedMoment(currentAddSubtitleBeginTimeEvent.moment, newAddSubtitleBeginTimeEvent.moment)) {
            this.currentEvent.youtubePlayerBorderEvent.addSubtitleBeginTimeEvent = _.clone(newAddSubtitleBeginTimeEvent);
            if (newAddSubtitleBeginTimeEvent.time !== null) {
                this.insertSubtitleElement(newAddSubtitleBeginTimeEvent.time, newAddSubtitleBeginTimeEvent.time + 1);
            }
            return;
        }
    };

    onExportButtonClicked = (e) => {
        let title = this.currentEvent.youtubePlayerBorderEvent.youtubeIdUpdateEvent.youtubeId;
        let content = this.refineTranscriptToXmlJson();
        let file = new Blob([content], {type: 'text/plain'});
        this.element.href = URL.createObjectURL(file);
        this.element.download = `${title}.xml`; // "myFile.xml";
        this.element.click();
    };

    onImportButtonClicked = (e) => {
        this.refs.fileUploader.click();
    };

    onChangeFile = (e) => {
        e.stopPropagation();
        e.preventDefault();
        let file = e.target.files[0];
        if (file)
            this.fileReader.readAsText(file);
    };

    refineTranscriptToXmlJson = () => {
        let targetJson = {
            "declaration": {
                "attributes": {
                    "version": "1.0",
                    "encoding": "utf-8"
                }
            },
            "elements": [
                {
                    "type": "element",
                    "name": "transcript",
                    "elements": [],
                }
            ]
        }
        let src = [...this.state.transcript];
        let elements = targetJson.elements[0].elements;
        src.forEach(e => {
            if (!e.english)
                return;
            console.log(e.kor);
            let ele = {
                "type": "element",
                "name": "text",
                "attributes": {
                    "start": e.start.toString(),
                    "dur": (e.end - e.start).toString(),
                    "learning": e.learn.toString(),
                    "kor": e.kor,
                },
                "elements": [
                    {
                        "type": "text",
                        "text": e.english,
                    }
                ]
            }
            elements.push(ele);
        });

        return convert.json2xml(targetJson, {compact: false, ignoreComment: true, spaces: 4});
    }

    createAction = () => {
        return { type: 'SubtitleBorder', payload: Object.assign({}, _.clone(this.sendingEvent)) };
    };

    htmlDecode = (input) => {
        this.dom.innerHTML = input;
        return this.dom.childNodes.length === 0 ? '' : this.dom.childNodes[0].nodeValue;
    };

    fetchTranscriptXml = async (youtubeId) => {
        let data = await fetch(`https://www.youtube.com/api/timedtext?v=${ youtubeId }&lang=en`, { method: 'GET' }).then(response => {
            return response.text();
        }).then(text => {
            return text;
        });

        if (data === "") {
            return [];
        }

        const transcript = await new Promise((resolve, reject) => xml2js.parseString(data, (err, result) => {
            if (err)
                reject(err);
            else
                resolve(result.transcript.text);
        }));

        transcript.map(ele => {
            ele.start = parseFloat(ele.$.start);
            ele.dur = parseFloat(ele.$.dur);
            ele.end = ele.start + ele.dur;
            ele.startStr = formatToHHmmss_ss(ele.start);
            ele.endStr = formatToHHmmss_ss(ele.end);
            ele.english = this.htmlDecode(ele._);
            ele.learn = false;
            ele.focus = false;
            ele.kor = "";
            delete ele.$;
            delete ele._;
            return ele;
        });

        return transcript;
    };

    removeSubtitleElement = (i) => {
        console.log('removeSubtitleElement ' + i);
        console.log(this.state.transcript);
        this.setState({
            transcript: [...this.state.transcript].filter((ele, idx) => {
                return idx !== i;
            }),
        });
    };

    componentDidUpdate(prevProps, prevState, snapshot) {
        console.log(this.state.transcript);
    }

    addSubtitleElement = (i) => {
        console.log('addSubtitleElement');
        var front = this.state.transcript[i];
        if (front)
            front = {end : 0};
        let rear = i < this.state.transcript.length-1 ? this.state.transcript[i+1] : null;
        let dur = rear === null? 0 : rear.start - front.end;
        console.log(front);
        let transcriptEle = {
            dur: dur,
            start: front.end,
            end: front.end + dur,
            startStr: formatToHHmmss_ss(front.end),
            endStr: formatToHHmmss_ss(front.end + dur),
            english: "",
            learn: false,
        };

        let copied = [...this.state.transcript];
        copied.splice(i+1, 0, transcriptEle);
        copied.join();
        this.setState({ transcript : copied });
    };

    insertSubtitleElement = (start, end) => {
        let transcriptEle = {
            start: start,
            end: end,
            dur: end - start,
            startStr: formatToHHmmss_ss(start),
            endStr: formatToHHmmss_ss(end),
            english: "",
            kor: "",
            learn: false,
        };
        // console.log('insertSubtitleElement');
        let copied = [...this.state.transcript];
        copied.push(transcriptEle);
        copied = this.getSortTranscripts(copied);
        this.setState({ transcript: copied });
    }

    playPhase = (phaseInfo) => {
        this.sendingEvent.playPhaseEvent.moment = moment();
        this.sendingEvent.playPhaseEvent.startTime = phaseInfo.startTime;
        this.sendingEvent.playPhaseEvent.endTime = phaseInfo.endTime;
        this.props.dispatch(this.createAction());
    };

    onStateChanged = (props, state) => {
        let target = [...this.state.transcript].map((e, i) => {
            if (i === props.index) {
                // console.log(state);
                e.english = state.english;
                e.kor = state.kor;
                e.start = state.start;
                e.startStr = state.startStr;
                e.end = state.end;
                e.endStr = state.endStr;
                e.learn = state.learn;
            }
            return e;
        });
        target = this.getSortTranscripts(target);
        this.setState({ transcript: target });
    };

    getSortTranscripts = (transcripts) => {
        if (transcripts.length < 2)
            return transcripts;
        transcripts.sort((a, b) => a.start - b.start);
        return transcripts;
    };

    onInsertSubtitleClicked = (e) => {
        var lastElement = this.state.transcript[this.state.transcript.length - 1];
        if (!lastElement)
            lastElement = {end: 0};
        this.insertSubtitleElement(lastElement.end, lastElement.end + 1);
    };

    onBeginInputBlur = (index) => {
    };

    onEndInputBlur = (index) => {
    };

    render() {
        return (
            <div style={ { position: 'absolute', marginLeft: '630px', minWidth: '565px', width:'auto'} }>
                <PanelGroup>
                    <Panel bsStyle="primary" style={ { height: '100%' } }>
                        <Panel.Heading style={ { fontSize: '16px' } }>
                            SUBTITLE
                        </Panel.Heading>
                        {
                            !this.state.loading &&
                            <Panel.Body style={ {
                                position: 'relative',
                                boxSizing: 'border-box',
                                height: '676px', // 655
                                padding: '0px',
                            } }>
                                <div style={ {
                                    overflow: 'scroll',
                                    height: '100%',
                                    borderWidth: '1px',
                                    borderColor: 'black',
                                    borderStyle: '',
                                } }>
                                    {
                                        this.state.transcript.map((ele, i) => {
                                            return <SubtitleBorderElement
                                                key={ i }

                                                english={ ele.english }
                                                index={ i }
                                                start={ ele.start }
                                                startStr={ele.startStr}
                                                end={ ele.end }
                                                endStr={ele.endStr}
                                                learn={ ele.learn }
                                                focus={ ele.focus }
                                                kor={ ele.kor }

                                                onStateChanged={ this.onStateChanged }
                                                onAddButtonClicked={ this.addSubtitleElement }
                                                onRemoveButtonClicked={ this.removeSubtitleElement }
                                                onPlayButtonClicked={ this.playPhase }
                                                onBeginInputBlur={this.onBeginInputBlur}
                                                onEndInputBlur={ this.onEndInputBlur }
                                            />;
                                        })
                                    }
                                </div>
                            </Panel.Body>
                        }
                        { this.state.loading &&
                            <Panel.Body
                                style={ {
                                    position: 'relative',
                                    boxSizing: 'border-box',
                                    height: '676px', // 655
                                    padding: '0px',
                                } }
                            >
                                <div
                                    style={ {
                                        position: 'absolute',
                                        top: '50%',
                                        left: '50%',
                                        transform: 'translate(-50%, -50%)'
                                    } }>
                                    <Loader
                                        type="Puff"
                                        color="#00BFFF"
                                        height="100"
                                        width="100"
                                    />
                                </div>
                            </Panel.Body>
                        }
                    </Panel>
                    <Panel>
                        <Panel.Heading>
                            <Panel.Body style={{position:'relative', height:'auto', padding:'0px'}}>
                                <input onChange={this.onChangeFile} type="file" id="file" ref="fileUploader" style={{display: "none"}}/>
                                <div style={ {position:'relative', height:'100%', marginBottom:'0px',borderWidth:'0px', borderColor:'red', borderStyle:'solid'} }>
                                    <Button style={{position:'relative', float:'right'}} bsSize="small" onClick={this.onExportButtonClicked}>EXPORT</Button>
                                    <Button style={{position:'relative', float:'right', marginRight:'5px'}} bsSize="small" onClick={this.onImportButtonClicked}>IMPORT</Button>
                                    <Button style={{position:'relative', float:'right', marginRight:'5px'}} bsSize="small" onClick={this.onInsertSubtitleClicked}>INSERT SUBTITLE</Button>
                                </div>
                            </Panel.Body>
                        </Panel.Heading>
                    </Panel>
                </PanelGroup>
                {/*<Spinner animation="border" variant="primary" />*/}
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {
        youtubePlayerBorderEvent: state.youtubePlayerBorderEvent.payload,
    };
};

export default connect(mapStateToProps)(SubtitleBorder);
