import { NOT_STARTED, CUED, PAUSED, PLAYING } from '../../const/YoutubePlayerState';

export default class ProgressPublisher {

    progressTimeId = null;
    youtubePlayer = null;
    onProgressCallback = null;

    eventQueue = [];

    onReady = (e) => {
        this.youtubePlayer = e.target;
    };

    onStateChanged = (e) => {
        let that = this;

        if (e.data === PLAYING) {
            this.progressTimeId = setInterval(function() {
                if (that.youtubePlayer) {
                    let currentTime = that.youtubePlayer.getCurrentTime();
                    if (that.onProgressCallback)
                        that.onProgressCallback(currentTime);
                    if (that.eventQueue.length > 0) {
                        let found = that.eventQueue.find(e => {
                            return currentTime >= e.targetTime;
                        });
                        if (found !== undefined) {
                            found.callback();
                            that.eventQueue.splice(that.eventQueue.indexOf(found), 1);
                        }
                    }
                }
            }, 10);
        } else {
            clearTimeout(this.progressTimeId);
        }
    };

    setOnProgress = (callback) => {
        this.onProgressCallback = callback;
    };

    setAlarm = (targetTime, callback) => {
        this.eventQueue.push({
            targetTime: targetTime,
            callback: callback
        });
    }
};
